import {Body} from './body';
import {Mat3, Vec2} from './math';
import {Renderer} from './renderer';
import {Color} from './renderer/color';
import * as UI from './ui';
import {GRAVITY_CONSTANT, World} from './world';

/** Multiplier of the time step passed to the update functions. */
const TIME_SCALE = 0.00001;

/**
 * Application class.
 */
export class App {
  /** Renderer used by the app. */
  private renderer: Renderer;

  /** Physics world. */
  private world: World;

  /** Reset button. */
  private resetButton: UI.Button;

  /** Settings button. */
  private settingsButton: UI.Button;

  /** Add body toggle. */
  private addBodyToggle: UI.Toggle;

  /** Remove body toggle. */
  private removeBodyToggle: UI.Toggle;

  /** Move camera toggle. */
  private moveCameraToggle: UI.Toggle;

  /** Input mode switch. */
  public inputMode: UI.Switch;

  /** Simulation speed slider. */
  private speed: UI.Slider;

  /** New body mass slider. */
  private mass: UI.Slider;

  /** Stores the first mouse down coordinates. Null if the mouse isn't down. */
  private mouseDown: Vec2|null = null;

  /** Stores the last mouse position. */
  private lastMousePosition: Vec2;

  /** Body being placed. */
  private placingBody: Body|null = null;

  // Default constructor.
  constructor() {
    // Initialize canvas
    const canvas = document.getElementById('canvas') as HTMLCanvasElement;
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;

    // Add canvas UI events
    canvas.addEventListener('mousedown', this.onMouseDown.bind(this));
    canvas.addEventListener('mouseup', this.onMouseUp.bind(this));
    canvas.addEventListener('mousemove', this.onMouseMove.bind(this));
    canvas.addEventListener('wheel', (event: WheelEvent) => {
      this.renderer.zoom(event.deltaY > 0 ? 1.1 : 0.9);
      event.preventDefault();
    });

    // Initialize UI buttons
    this.resetButton = new UI.Button(document.getElementById('resetButton') as HTMLButtonElement);
    this.settingsButton = new UI.Button(document.getElementById('settingsButton') as HTMLButtonElement);

    // Initialize UI toggles and switch
    this.addBodyToggle = new UI.Toggle(document.getElementById('addBodyToggle') as HTMLButtonElement);
    this.removeBodyToggle = new UI.Toggle(document.getElementById('removeBodyToggle') as HTMLButtonElement);
    this.moveCameraToggle = new UI.Toggle(document.getElementById('moveCameraToggle') as HTMLButtonElement);
    this.inputMode = new UI.Switch();
    this.inputMode.add('addBody', this.addBodyToggle);
    this.inputMode.add('removeBody', this.removeBodyToggle);
    this.inputMode.add('moveCamera', this.moveCameraToggle);

    // Initialize UI sliders
    this.speed = new UI.Slider(
        document.getElementById('speedInput') as HTMLInputElement, document.getElementById('speedValue') as HTMLElement,
        'exponential');
    this.speed.value = 1.0;
    this.mass = new UI.Slider(
        document.getElementById('massInput') as HTMLInputElement, document.getElementById('massValue') as HTMLElement,
        'exponential');
    this.mass.value = 1.0;

    // Create renderer.
    this.renderer = new Renderer(canvas);

    // Create physics world.
    this.world = new World();

    // Create a few bodies.
    const CENTRAL_MASS = 1000.0;
    this.world.addBody(new Body(CENTRAL_MASS, new Vec2(0.0, 0.0), new Vec2(0.0, 0.0), Color.random()));

    for (let i = 0; i < 100; ++i) {
      const mass = 0.1;
      const angle = (i / 100.0) * Math.PI * 2;
      const distance = 1.0;
      const pos = new Vec2(Math.cos(angle) * distance, Math.sin(angle) * distance);
      const vel = pos.perpendicular().normalize().mul(Math.sqrt(GRAVITY_CONSTANT * (CENTRAL_MASS) / distance));
      this.world.addBody(new Body(mass, pos, vel, Color.random()));
    }
  }

  /**
   * Starts the application's main loop.
   */
  public start() {
    this.animate(0.0);
  }

  /**
   * Updates and draws the application.
   * @param dt Time step in milliseconds.
   */
  private animate(dt: number): void {
    this.world.update(this.speed.value * dt * TIME_SCALE);

    if (this.inputMode.current == 'addBody' && (this.lastMousePosition || this.mouseDown)) {
      const pos = this.renderer.screenToWorld(this.mouseDown ? this.mouseDown : this.lastMousePosition);
      if (!this.placingBody)
        this.placingBody = new Body(this.mass.value, pos, new Vec2(0.0, 0.0), Color.random());
      this.placingBody!.position = pos;
      this.placingBody!.setMass(this.mass.value);
      this.renderer.drawCircle(pos, this.placingBody!.getRadius(), this.placingBody!.color);
    }
    this.world.draw(this.renderer);
    this.renderer.flush();

    window.requestAnimationFrame(this.animate.bind(this));
  }

  /**
   * Mouse down event listener.
   * @param event The mouse event.
   */
  private onMouseDown(event: MouseEvent): void {
    if (event.button != 0) return;
    this.mouseDown = new Vec2(event.offsetX, event.offsetY);
    this.lastMousePosition = this.mouseDown;
    event.preventDefault();
  }

  /**
   * Mouse up event listener.
   * @param event The mouse event.
   */
  private onMouseUp(event: MouseEvent): void {
    if (event.button != 0) return;

    const mousePosition = new Vec2(event.offsetX, event.offsetY);
    let delta = mousePosition.sub(this.mouseDown!);
    delta.y *= -1.0;

    switch (this.inputMode.current) {
      case 'addBody':
        this.placingBody!.velocity = delta.mul(0.001);
        this.world.addBody(this.placingBody!);
        this.placingBody = null;
        break;
      case 'removeBody':
        this.world.removeBody(this.renderer.screenToWorld(mousePosition));
        break;
    }

    this.mouseDown = null;
    event.preventDefault();
  }

  /**
   * Mouse move event listener.
   * @param event The mouse event.
   */
  private onMouseMove(event: MouseEvent): void {
    const mousePosition = new Vec2(event.offsetX, event.offsetY);
    if (!this.lastMousePosition) this.lastMousePosition = mousePosition;
    const delta = mousePosition.sub(this.lastMousePosition);
    this.lastMousePosition = mousePosition;

    if (this.mouseDown !== null && this.inputMode.current == 'moveCamera') {
      this.renderer.move((new Vec2(delta.x, -delta.y)).mul(0.001));
    }

    event.preventDefault();
  }
}
